<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5(data-aos="fade")
    .titulo-principal.color-acento-contenido
      .titulo-principal__numero
        span 3
      h1 Estados financieros
    .row
      .col-lg-6(data-aos="fade-up")
        img(src='@/assets/curso/tema3/img1.png', alt='Texto que describa la imagen')

      .col-lg-6
        p Estos son una coleccin de informes a nivel de resumen sobre los resultados financieros, la posicin financiera y los flujos de efectivo de una organizacin, incluyen:
        .col-sm.mb-5.mb-sm-0
          ul.lista-ul.mb-0 
            li 
              i.fas.fa-angle-right.color3
              | El estado de resultados.
          ul.lista-ul.mb-0 
            li
              i.fas.fa-angle-right.color3
              | El balance general.
          ul.lista-ul.mb-0
            li
              i.fas.fa-angle-right.color3
              | El estado de flujos de efectivo.
    p.text-center.mt-4 <strong> Los estados financieros son tiles por las siguientes razones:</strong>
    img.mt-5(src='@/assets/curso/tema3/img2.svg', alt='Texto que describa la imagen')(data-aos="zoom-in")
    .row.mt-5
      .col-lg-9.mt-5
        .row
          .col-lg-8
            p Existen pocas desventajas en la emisin de estados financieros, una posible preocupacin que podra presentarse es que pueden manipularse de manera fraudulenta, lo que lleva a los inversores a creer que la entidad emisora ha producido mejores resultados de los que realmente obtuvo. Tal manipulacin tambin podra llevar a un prestamista a emitir deuda a una empresa que no puede pagarla de manera realista.
          .col-lg-4
            img(src='@/assets/curso/tema3/img4.png', alt='Texto que describa la imagen')
        .row.azq.p-2
          p Uno de los estados financieros ms representativos es el <strong>Balance General:</strong> donde se presentan los activos, pasivos y capital contable de una entidad a la fecha del informe, relacionando el total de todos los activos y donde debe coincidir con el total combinado de todos los pasivos y el patrimonio. La informacin de activos en el balance general se subdivide en activos corrientes y a largo plazo. De igual manera, la informacin de pasivos se subdivide en pasivos corrientes y de largo plazo. 
        p Esta estratificacin es til para determinar la liquidez de un negocio, donde idealmente, el total de todos los activos corrientes debe exceder el total de todos los pasivos corrientes, lo que implica que si una empresa tiene activos suficientes para pagar sus obligaciones corrientes, tambin se usa para comparar los niveles de deuda con la cantidad de capital invertido en el negocio, para ver si su nivel de apalancamiento es apropiado.
      .col-lg-3.mt-5(data-aos="fade-left")
        img(src='@/assets/curso/tema3/img3.png', alt='Texto que describa la imagen')
    p.mt-4 El estado muestra lo que posee el negocio, lo que debe y el capital que se ha invertido, y el Balance de una empresa presenta la siguiente estructura: 
    .row.justify-content-center.mt-5
      .col-lg-8
        .titulo-sexto.color-acento-contenido
          h5 Tabla 6.
          span Ejemplo de Balance  
        .tabla-a.color-acento-contenido.mb-5 
          table
            thead
              tr
                <th colspan="2"> ACTIVO</th>
                <th colspan="2"> PASIVO / PATRIMONIO</th>

            tbody
              tr
                td Activo circulante
                td $4.000.000,00
                td Pasivo Circulante
                td $1.200.000,00 
              tr
                td Activo fijo
                td $1.500.000,00 
                td Pasivo fijo
                td $ 900.000,00
              tr
                td Otros activos
                td $1.500.000,00
                td Otros Pasivos
                td $2.100.000,00 
              tr
                td TOTAL ACTIVO
                td $7.000.000,00 
                td Total Pasivo
                td $ 4.200.000,00 
              tr
                td 
