                td Capital contable
                td $ 2.800.000,00           
              tr
                td 
                td 
                td TOTAL PASIVO + PATRIMONIO
                td   $7.000.000,00 
    p Este se divide en dos lados (o secciones) el lado izquierdo del balance general describe todos los activos y el lado derecho describe los pasivos de la empresa y el patrimonio de los accionistas.
    p Los activos y pasivos se separan en dos categoras: 
    .row.justify-content-center
      .col-lg-10
        .row.mb-5
          .col-sm-6.col-lg-6.mb-4.mb-lg-0
            .tarjeta--boton.color-secundario.p-4
              .row.justify-content-center.mb-3
                .col-6
                  figure
                    img(src='@/assets/curso/tema3/ico1.svg', alt='Texto que describa la imagen')
              h4.text-center Activos y pasivos corrientes

              p.text-center.mb-5.mt-4 Tales como inventario, efectivo y cuentas comerciales y se ubican en la seccin de las cuentas lquidas porque se establecen a corto y mediano plazo.
          .col-sm-6.col-lg-6.mb-4.mb-lg-0
            .tarjeta--boton.color-secundario.p-4
              .row.justify-content-center.mb-3
                .col-6
                  figure
                    img(src='@/assets/curso/tema3/ico2.svg', alt='Texto que describa la imagen')
              h4.text-center Activos y pasivos no corrientes

              p.text-center.mb-5.mt-4 Tales como planta, propiedad y equipo y deudas extensas de tiempo, se colocan en la seccin de cuentas ilquidas porque se establecen a largo plazo.
    .cajon.color-primario.p-4.mb-4
      p El balance general es un estado financiero muy importante, junto con otros informes como el estado de resultados y el estado de flujo de efectivo, permiten obtener una imagen completa de la salud de una empresa.
    .row.mt-5
      .col-lg-7
        .row
          .col-lg-6
            .row
              .col-lg-7.fon1
                p.mt-2.mb-0 <strong> El estado de resultados </strong>
              .col-lg-2.tt
                img(src='@/assets/curso/tema2/ico1.svg', alt='Texto que describa la imagen')  
    .row.mt-4
      .col-lg-7
        p Muestra los resultados de las operaciones y actividades financieras de una entidad para el perodo sobre el que se informa, por lo general, contiene los resultados del ltimo mes o del ltimo ao, y puede incluir varios perodos con fines comparativos. Su estructura general comienza con todos los ingresos generados, de los cuales se resta el costo de los bienes vendidos, y luego todos los gastos de venta, generales y administrativos, el cual indicar como resultado una ganancia o una prdida neta de impuestos sobre la renta.
        p Este trata de determinar el monto por el cual los ingresos contables superan a los gastos contables, y al remanente se le llama resultado, el cual puede ser positivo o negativo. Si es positivo se le llama utilidad y si es negativo se le denomina prdida.  El formato del estado de resultados se compone de: 
        .col-sm.mb-5.mb-sm-0
          ul.lista-ul.mb-0 
            li 
              i.fas.fa-angle-right
              | Encabezado.     
          ul.lista-ul.mb-0 
            li 
              i.fas.fa-angle-right
              | Seccin de ingresos.   
          ul.lista-ul.mb-0 
            li 
              i.fas.fa-angle-right
              | Seccin de gastos.  
          ul.lista-ul.mb-0 
            li 
              i.fas.fa-angle-right
              | Saldo de utilidad neta o prdida neta.             
      .col-lg-5(data-aos="fade-up-right")
        img(src='@/assets/curso/tema3/img5.png', alt='Texto que describa la imagen')  
    .row.justify-content-center.mt-4
      .col-lg-10
        .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-5(data-aos="fade-up")
          .bloque-texto-g__img(
            :style="{'background-image': `url(${require('@/assets/curso/tema3/img6.png')})`}"
          )
          .bloque-texto-g__texto.p-4
            p.mb-0 Si los ingresos son mayores que los gastos la diferencia se llama utilidad neta, la utilidad neta aumenta el capital, pero si los gastos son mayores que los ingresos, la compaa habra incurrido en una prdida neta y consecuentemente habr una disminucin en la cuenta de capital.
        p.mt-4 El Estado de Resultados puede tener pequeas variaciones entre las diferentes empresas, debido a que los gastos e ingresos dependen del tipo de operaciones o negocios realizados. Sin embargo, existen varias partidas genricas que se ven comnmente en cualquier estado de resultados.
    p.mt-4 Las partidas ms comunes del Estado de Resultados incluyen:
    .row.mt-4
      .col-lg-6
        .row.mb-5
          .col-md-12.mb-5.mb-md-0
            LineaTiempoD.color-secundario
              p(numero="1" titulo="Ingresos/Ventas") Son los ingresos de la empresa por ventas o servicios, que se muestran en la parte superior del estado de cuenta. Este valor ser el bruto de los costes asociados a la creacin de los bienes vendidos a la prestacin de los servicios. Algunas empresas tienen mltiples fuentes de ingresos que se suman a una lnea de ingresos total.
              p(numero="2" titulo="Costo de los bienes vendidos") Es un elemento de lnea que agrega los costos directos asociados con la venta de productos para generar ingresos, tambin se puede llamar Costo de ventas si la empresa es un negocio de servicios. Los costos directos pueden incluir mano de obra, piezas, materiales y una asignacin de otros gastos, como la depreciacin.              
              p(numero="3" titulo="Ganancia bruta") Se calcula restando el costo de los bienes vendidos (o el costo de las ventas) de los ingresos por ventas.
              p(numero="4" titulo="Gastos de <i>marketing</i>, publicidad y promocin") La mayora de las empresas tienen algunos gastos relacionados con la venta de bienes y/o servicios. Los gastos de <i>marketing</i>, publicidad y promocin a menudo se agrupan porque son gastos similares, todos relacionados con la venta.
              p(numero="5" titulo="Gastos generales y administrativos") Los gastos de venta, generales y administrativos se incluyen la seccin de ventas, general y administrativa que contiene todos los dems costos indirectos asociados con el funcionamiento del negocio. Esto incluye sueldos y salarios, alquiler y gastos de oficina, seguros, gastos de viaje y, en ocasiones, depreciacin y amortizacin, junto con otros gastos operativos. Sin embargo, las entidades pueden optar por separar la depreciacin y la amortizacin en su propia seccin.
              p(numero="6" titulo="Ganancias antes de intereses, impuestos, depreciacin y amortizacin") Si bien no est presente en todos los estados de resultados, se calcula restando los gastos de venta, generales y administrativos (excluyendo la amortizacin y la depreciacin) de la ganancia bruta.
      .col-lg-6
        img(src='@/assets/curso/tema3/img7.png', alt='Texto que describa la imagen')  
    .row.mt-4
      .col-lg-6
        img(src='@/assets/curso/tema3/img8.png', alt='Texto que describa la imagen')  
      .col-lg-6
        .row.mb-5
          .col-md-12.mb-5.mb-md-0
            LineaTiempoD.color-secundario
              p(numero="7" titulo="Gastos de depreciacin y amortizacin") Son gastos que no son en efectivo, y los crean los contadores para distribuir el costo de los activos de capital, como propiedades, planta y equipo.
              p(numero="8" titulo="Utilidad operativa") Es la ganancia antes de restar de los ingresos cualquier ingreso no operativo, gastos no operativos, intereses o impuestos. EBIT es un trmino comnmente utilizado en finanzas y significa ganancias antes de intereses e impuestos.
              p(numero="9" titulo="Ingresos antes de impuestos") Significa ganancias antes de impuestos, y se calcula restando los gastos por intereses de los ingresos operativos. Este es el subtotal final antes de llegar al ingreso neto.
              p(numero="10" titulo="Inters") Es comn que las empresas dividan los gastos por intereses y los ingresos por intereses en una lnea separada en el estado de resultados. Esto se hace para conciliar la diferencia entre EBIT y EBT. El gasto por intereses est determinado por el programa de la deuda.
              p(numero="11" titulo="Otros gastos") Las empresas a menudo tienen otros gastos que son exclusivos de su industria. Otros gastos pueden incluir cumplimiento, tecnologa, investigacin y desarrollo (I+D), compensacin basada en acciones (SBC), cargos por deterioro, ganancias/prdidas en la venta de inversiones, impactos cambiarios y muchos otros gastos que son especficos de la industria o de la empresa. 
              p(numero="12" titulo="Impuestos sobre la Renta") Se refieren a los impuestos relevantes que se cargan sobre la renta antes de impuestos. El gasto fiscal total puede consistir tanto en impuestos actuales como en impuestos futuros.
    .row.mt-5
      .col-lg-9
        .row
          .col-lg-6
            .row
              .col-lg-7.fon1
                p.mt-2.mb-0 <strong> El estado de flujos de efectivo </strong>
              .col-lg-2.tt
                img(src='@/assets/curso/tema2/ico1.svg', alt='Texto que describa la imagen')  
    .row.mt-4
      .col-lg-7
        p Es el estado financiero final, muestra los cambios en los flujos de efectivo de una entidad durante el perodo sobre el que se informa, estos flujos se dividen en flujos de efectivo de actividades operativas, actividades de inversin y actividades de financiacin, la mayor parte generalmente se enumeran en la seccin de actividades operativas, que establece las entradas y salidas de efectivo relacionadas con las operaciones bsicas del negocio, como los cambios en los saldos de cuentas por cobrar, inventario y cuentas por pagar. La seccin de actividades de inversin contiene los flujos de efectivo de la compra o venta de instrumentos de inversin, activos u otros negocios y la seccin de actividades de financiacin contiene flujos de efectivo relacionados con la adquisicin o pago de deuda, emisin de dividendos, venta de acciones, etc.
        p Cuando los estados financieros se emitan a terceros, se deben incluir las notas complementarias, estas incluyen explicaciones de diversas actividades, detalles adicionales sobre algunas cuentas y otros elementos segn lo exige el marco contable aplicable, como las NIIF. El nivel y los tipos de detalles proporcionados dependen de la naturaleza del negocio de la entidad emisora y los tipos de transacciones en las que participe.
        p Si una empresa planea emitir Estados financieros para usuarios externos (como inversionistas o prestamistas) estos deben presentarse de acuerdo con los principales marcos contables, que permiten cierto margen de maniobra en la forma en que se pueden estructurar los estados financieros, por lo que es probable que los estados emitidos por diferentes empresas, incluso en la misma industria, tengan apariencias algo diferentes. 
      .col-lg-5(data-aos="fade-left")
        img(src='@/assets/curso/tema3/img9.png', alt='Texto que describa la imagen')  
    .row.mt-5
      .col-lg-3
        img(src='@/assets/curso/tema3/img10.png', alt='Texto que describa la imagen')  
      .col-lg-9
        .cajon.color-acento-contenido.p-4.mb-4
          p Los estados financieros que se emiten a terceros pueden ser auditados para verificar su exactitud y equidad de presentacin.
    p Si los estados financieros se emiten estrictamente para uso interno, no existen pautas, aparte del uso comn, sobre cmo se deben presentar los estados, como mnimo, se espera que una empresa emita un estado de resultados y un balance general para documentar sus resultados mensuales y su situacin financiera final. Se espera el conjunto completo de estados financieros cuando una empresa informa los resultados de un ao fiscal completo, o cuando una empresa que cotiza en bolsa informa los resultados de sus trimestres fiscales.
    separador
    #t_3_1.titulo-segundo.color-acento-contenido
      h2 3.1  Caractersticas y componentes
    p Una de las primeras consideraciones para tener en cuenta es la necesidad de disponer de un esquema general para entender las finanzas de la empresa, este debe permitir comprender las interacciones que se producen entre las inversiones, el financiamiento y el rendimiento de la empresa. 
    .row
      .col-lg-7
        p Con el esquema claro, se debe saber relacionar el modelo de negocio con los Estados Financieros, la realidad del negocio debe verse reflejada en los estos y dar cuenta de: cmo se vende, cunta es la inversin en existencias necesarias para ofrecer adecuadamente los productos y con qu financiamiento de los proveedores se cuenta.
        .row.p-2.backga
          p Es muy importante tener claro que, para poder obtener un rendimiento econmico, se debe considerar que es necesario invertir, sin las inversiones, lamentablemente, la mayora de las veces no es posible obtener la venta de un producto o un servicio, por eso es necesario saber y gestionarlas adecuadamente para que sean inversiones eficientes. Adems, para sostenerlas, es imprescindible un adecuado financiamiento en las condiciones y plazos convenientes para poder cumplir con los objetivos trazados.
      .col-lg-5
        img(src='@/assets/curso/tema3/img11.png', alt='Texto que describa la imagen')  
    p.mt-5 Por eso, es necesario entender las finanzas de la empresa, porque facilita comprender los mecanismos para garantizar la sostenibilidad, lograr la rentabilidad y una adecuada liquidez para protegerla en contextos difciles.
    p Una de las herramientas ms importantes para obtener el diagnstico financiero de una empresa en un perodo de tiempo determinado es el <strong>Flujo de Fondos</strong>, llamado tambin <strong>Cuadro de Fuentes y Usos</strong> o Estado de Orgenes y Aplicaciones, esta herramienta responde las siguientes preguntas:
    .row.justify-content-center.mt-5
      .col-lg-10
        .row
          .col-lg-6
            .row
              .col-lg-12
                .tarjeta-avatar-b.mb-5
                  .tarjeta-avatar-b__img
                    img(src='@/assets/curso/tema3/ico3.svg' alt='AvatarTop')
                  .tarjeta.tarjeta--azul.aqw
                    .p-4
                      h5.text-center En qu se ha usado el dinero y de dnde se ha obtenido?
          .col-lg-6
            .row
              .col-lg-12
                .tarjeta-avatar-b.mb-5
                  .tarjeta-avatar-b__img
                    img(src='@/assets/curso/tema3/ico3.svg' alt='AvatarTop')
                  .tarjeta.tarjeta--azul.aqw
                    .p-4
                      h5.text-center.mb-4 Dnde estn las utilidades que hemos obtenido? 
        .cajon.color-acento-contenido.p-4.mb-4
          p En sntesis, responde qu se hizo con el dinero en un perodo y cmo se financi, es una herramienta muy poderosa para entender cmo han sido los movimientos de dinero en la empresa en un perodo determinado.
    separador  
    #t_3_2.titulo-segundo.color-acento-contenido
      h2 3.2  Frmulas de liquidacin  
    p Es una relacin entre dos cuentas o grupo de cuentas del mismo estado financiero o de dos estados financieros, sealan los puntos fuertes y dbiles de un negocio, el anlisis de razones de los estados financieros de una empresa es importante para sus accionistas, sus acreedores y para la propia gerencia, las cuales son:         
    .row.justify-content-center
      .col-lg-8
        ImagenInfograficaB.color-acento-botones.mb-5
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/tema3/img12.svg', alt='Texto que describa la imagen')


          div(x="30%" y="4%" tooltip="Razones de liquidez" )
            .row.justify-content-center.mt-5
              .row.info1
                h5.mt-4 Razones de liquidez 
              .row.info2
                p.mb-0.mt-3 Es un tipo de ndice financiero utilizado para determinar la capacidad de una empresa para pagar sus obligaciones de deuda a corto plazo. La mtrica ayuda a determinar si una empresa puede utilizar sus activos corrientes o lquidos para cubrir sus pasivos corrientes.
                p Generalmente se utilizan tres ndices de liquidez: el ndice actual, el ndice rpido y el ndice de efectivo. En cada una de las razones de liquidez, el monto del pasivo circulante se coloca en el denominador de la ecuacin y el monto del activo lquido se coloca en el numerador.

          div(x="70%" y="4%" tooltip="Razn corriente ")
            .row.justify-content-center.mt-5
              .row.info1
                h5.mt-4 Razn corriente: 
              .row.info2
                p.mb-0.mt-3 Determina la capacidad de la empresa para cumplir sus obligaciones a corto plazo, se expresa de la manera siguiente: 
                .row.justify-content-center
                  .col-lg-8.mt-2.fon22
                    p.mb-0.text-center <i><strong>Razn corriente = Activo circulante/Pasivo circulante.</strong></i>
                .cajon.color-acento-contenido.p-4.mb-4.mt-4
                  h5.mb-0 Importante: 
                  p.mb-0 Siempre que la razn circulante de una empresa sea 1 el capital de trabajo neto ser 0. 
                    
          div(x="97%" y="32%" tooltip="Razn rpida (Prueba cida) ")
            .row.justify-content-center.mt-5
              .row.info1
                h5.mt-4 Razn rpida (Prueba cida): 

              .row.info2
                p.mb-0.mt-3 Este es uno de los tipos de ratios financieros ms utilizados, ya que proporciona un indicador rpido de la liquidez empresarial, para calcularla se debe restar el inventario actual de los activos actuales y luego dividirlo entre los pasivos:
                .row.justify-content-center.mt-2
                  .col-lg-8.mt-2.fon22
                    p.mb-0.text-center <i><strong>(Activo corriente  Inventario) / Pasivo corriente</strong></i>
                p.mt-4 Esto le muestra con qu facilidad las deudas a corto plazo de una empresa se cubrirn con sus activos lquidos o efectivo existentes. 
                p Si la razn rpida es mayor que 1, el negocio est en una buena posicin financiera.
          div(x="96%" y="70%" tooltip="Capital de trabajo neto ")
            .row.justify-content-center.mt-5
              .row.info1
                h5.mt-4 Capital de trabajo neto: 
              .row.info2
                p.mt-3 Otro ndice relacionado con los pasivos es el ndice de capital de trabajo, tambin conocido como ndice circulante, al igual que la relacin rpida, analiza qu tan bien una empresa puede pagar sus deudas existentes. Sin embargo, considera todos los activos actuales en lugar de simplemente los activos lquidos, por lo que no es necesario restar el inventario de la ecuacin. 
                p El ndice de capital de trabajo se ve as cuando se escribe como una frmula: 
                .row.justify-content-center.mt-2
                  .col-lg-8.mt-2.fon22
                    p.mb-0.text-center <i><strong>Activo corriente / Pasivo corriente</strong></i>
                p.mt-4 Cuanto mayor sea el ndice de capital de trabajo, ms fcil ser para una empresa pagar sus deudas utilizando sus activos circulantes.
          div(x="3%" y="70%" tooltip="Razones de endeudamiento o solvencia ")
            .row.justify-content-center.mt-5
              .row.info1
                h5.mt-4 Razones de endeudamiento o solvencia: 

              .row.info2
                p.mt-3 Tambin conocidos como el ndice de apalancamiento, es cualquier tipo de ndice financiero que indica el nivel de deuda contrada por una entidad comercial contra varias otras cuentas en su balance general, estado de resultados o estado de flujo de efectivo. Estos ndices proporcionan una indicacin de cmo se financian los activos y las operaciones comerciales de la empresa (utilizando deuda o capital). 

                p.mb-0 Algunas cuentas que se considera que tienen una comparabilidad significativa con la deuda son los activos totales, el patrimonio total, los gastos operativos y los ingresos.
                p A continuacin, se muestran 5 de los ndices de apalancamiento ms utilizados:
                .col-sm.mb-5.mb-sm-0
                  ul.lista-ul
                    li.mb-0 
                      i.fas.fa-angle-right.color3
                      | Relacin deuda-activos = Deuda total / Activos totales
                    li.mb-0 
                      i.fas.fa-angle-right.color3
                      | Relacin deuda-capital = Deuda total / Patrimonio total
                    li.mb-0 
                      i.fas.fa-angle-right.color3
                      | Relacin deuda-capital = Deuda actual / (Deuda total + Patrimonio total)
                    li.mb-0 
                      i.fas.fa-angle-right.color3
                      | Razn de deuda a EBITDA = Deuda total / Ganancias antes de intereses Impuestos Depreciacin y amortizacin (EBITDA)
                    li.mb-0 
                      i.fas.fa-angle-right.color3
                      | Relacin de activos a capital = Activos totales / Patrimonio total
          div(x="3%" y="32%" tooltip="Razones de rentabilidad ")
            .row.justify-content-center.mt-5
              .row.info1
