        .row.aq1.p-3
          p.mb-0 Las NIIF fomentan una mayor transparencia empresarial, especifican en detalle cmo las empresas deben mantener sus registros y reportar sus gastos e ingresos. Se establecieron para crear un lenguaje contable comn que pudiera ser entendido globalmente por inversionistas, auditores, reguladores gubernamentales y otras partes interesadas.
