        p Fueron desarrollados por el Consejo de Normas Internacionales de Contabilidad, que forma parte de la Fundacin IFRS sin fines de lucro con sede en Londres, estableciendo los estndares para brindar transparencia, responsabilidad y eficiencia a los mercados financieros de todo el mundo, estas tambin tienen diferentes requisitos para informar los gastos. Por ejemplo, si una empresa est gastando dinero en desarrollo o en inversiones para el futuro, no necesariamente tiene que declararse como un gasto. Se puede escribir en mayscula en su lugar.
